

import "./index.scss"
import Head  from "~/components/Head";
import {connect} from "react-redux"
import { getGoodTypes, getGoodList } from "../../actions";
import { Tabs, WhiteSpace, Badge } from 'antd-mobile';
import List from "~/components/list"
@connect(
    state=>{
        return {
            goodTypes:state.getIn(['data','goodTypes']),
            goodList:state.getIn(['data','goodList'])

        }
    }
)
export default class Classify extends Component{

    componentDidMount(){
        if(!this.props.goodTypes.length){
            this.props.dispatch(getGoodTypes({
                url:"/vue/getGoodTypes",

            }))
        }

        if(!this.props.goodList.length){
            this.props.dispatch(getGoodList({
                url:"/vue/getGoods"
            }))
        }

    }

    render(){
        const {
            goodTypes,
            goodList
        } = this.props;
        console.log(this.props);
        
        return (
            <div className="indexview">
                <Head title="分类" show={true} ></Head>
                <Tabs
                    tabs={goodTypes}
                    initialPage={0}
                    tabBarActiveTextColor="red"
                    tabBarUnderlineStyle={{border:"2px solid red"}}
                >
                    {
                        goodTypes.map((type,i)=>{
                            return (
                                <List 
                                    key={i}
                                    type={type}
                                    good={goodList.filter(item=>item.type.value==type.value)}
                                >  

                                </List>
                            )
                        })
                    }
                </Tabs>
            </div>
        )
    }
}