

import "./index.scss"
import { PullToRefresh, WingBlank,WhiteSpace } from 'antd-mobile';
import {connect} from "react-redux"
import { reverserGoodList, reverserSearchList } from "../../actions";
import ListItem from "../listItem"
@connect()
export default class List extends Component{
    constructor(){
        super();
        this.state = {
            refreshing:false , // 是否正刷新 
        }
    }
    render(){
        const {
            good,
            keys
        } = this.props;
        return (
            <div>
                <PullToRefresh
                    damping={60}
                    ref={el => this.ptr = el}
                    indicator={ { deactivate: '上拉可以刷新' }}
                    direction={ 'down'}
                    refreshing={this.state.refreshing}
                    onRefresh={() => {
                        this.setState({ refreshing: true });
                        setTimeout(() => {
                            this.setState({ refreshing: false });
                            if(keys=="search"){
                                this.props.dispatch(reverserSearchList());
                            }else{
                                // 翻转 goodList 
                                this.props.dispatch(reverserGoodList());
                            }
                            
                        }, 1000);
                    }}
                >
                    {
                        good.map((g,i)=>{
                            return (
                                <ListItem key={i} g={g} > </ListItem>
                            )
                        })
                    }
                </PullToRefresh>
            </div>
        )
    }
}