
import "./index.scss";
import {  WingBlank,WhiteSpace } from 'antd-mobile';
import {Link} from "react-router-dom"


export default class ListItem extends Component{
    render(){
        const {
            g
        } = this.props;
        return (
            <div  style={{width:"100%"}} className="move-in">
                <Link to={"/detail/"+g._id+"?name="+g.name} >
                    <WingBlank>
                        <WhiteSpace/>
                        <img className="l-img"  src={g.img} alt=""/>
                        <h2 className="l-text"> {g.name} -- {g.type.value} / RMB =={g.price} </h2>
                    </WingBlank>
                </Link>
            </div>
        )
    }
}